.container {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  max-width: 1200px;
  flex-wrap: wrap;
  z-index: 1;
  
} 

.container .card {
  position: relative;
  width: 480px;
  height: 400px;
  margin: 30px;
  box-shadow: 20px 20px 50px rgba(0,0,0,0.5);
  border-radius: 15px;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
  border-top:1px solid rgba(255,255,255,0.5);
  border-left:1px solid rgba(255,255,255,0.5);
  backdrop-filter: blur(5px);


}

.container .card .content {
 padding: 20px;
 text-align: center; 
 transform: translateY(100px);
 opacity:0 ;
 transition: 0.5s;
 
}

.container .card:hover .content {
  
  transform: translateY(0);
  opacity:1 ;
  

 }



.container .card .content h2 {
  position: absolute;
  top:-100px;
  right: -30px;
  font-size: 6em;
  color: rgba(255,255,255,0.05);
  pointer-events: none;
}

.container .card .content h3 {
  font-size: 1.8em;
  color: #fff;
  z-index:1;

}

.container .card .content p {
  font-size: 1em;
  color: #fff;
  font-weight: 300;

}



.glare-wrapper {
  position: absolute;
  top: 32px;
  left: 28px;
  width: 83%;
  height: 86%;
  overflow: hidden;
}